.container {
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;

    @include mobile {
        max-width: 480px;
    }

    @include tablet {
        max-width: 768px;
        padding-left: 32px;
        padding-right: 32px;
    }

    @include desktop {
        max-width: 1200px;
        padding-left: 16px;
        padding-right: 16px;
    }

}