}

body {
  font-family: Montserrat, sans-serif;
}

h1, h2, h3 {
  font-family: Seymour One, sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.link {
  cursor: url("cursor.d8582cc2.svg"), auto;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.list {
  list-style: none;
}

.no-scroll {
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.btn {
  cursor: url("cursor.d8582cc2.svg"), auto;
  border: none;
  padding: 0;
  font-family: inherit;
